import { Component, ViewChild } from '@angular/core';
import { IonicPage, Slides, NavController, MenuController } from 'ionic-angular';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';

@IonicPage({
  name: 'page-initial',
  segment: 'initial',
  priority: 'high'
})

@Component({
  selector: 'page-initial',
  templateUrl: 'initial.html',
})

export class InitialPage {
  counter: number = parseInt(localStorage.getItem('state'));
  @ViewChild(Slides) slides: Slides;
  showSkip = true;
  dir: string = 'ltr';
  client_name: any;
  txt: string = "";
  slideList: Array<any> = [];
  //dreamblockmainicon: any;
  showLoginButton: boolean = false;



  constructor(public navCtrl: NavController, public menu: MenuController, public translate: TranslateService) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');

    this.translate.get('Initial-page-1.slideList').subscribe((translated: string) => {
      var title1 = this.translate.instant('Initial-page-1.slideList.title1');
      var title2 = this.translate.instant('Initial-page-1.slideList.title2');
      var title3 = this.translate.instant('Initial-page-1.slideList.title3');
      var title4 = this.translate.instant('Initial-page-1.slideList.title4');
      var title5 = this.translate.instant('Initial-page-1.slideList.title5');
      var description1 = this.translate.instant('Initial-page-1.slideList.description1');
      var description2 = this.translate.instant('Initial-page-1.slideList.description2');
      var description3 = this.translate.instant('Initial-page-1.slideList.description3');
      var description4 = this.translate.instant('Initial-page-1.slideList.description4');
      var description5 = this.translate.instant('Initial-page-1.slideList.description5');
      var icon1 = this.translate.instant('Initial-page-1.slideList.icon1');
      var icon2 = this.translate.instant('Initial-page-1.slideList.icon2');
      var icon3 = this.translate.instant('Initial-page-1.slideList.icon3');
      var icon4 = this.translate.instant('Initial-page-1.slideList.icon4');
      var icon5 = this.translate.instant('Initial-page-1.slideList.icon5');

      this.slideList = [

        {
          title: title1,
          description: description1,
          image: icon1,
          id: 0
        },


        {
          title: title2,
          description: description2,
          image: icon2,
          id: 1
        },
        {
          title: title3,
          description: description3,
          image: icon3,
          id: 2
        },
        {
          title: title4,
          description: description4,
          image: icon4,
          id: 3
        },
        {
          title: title5,
          description: description5,
          image: icon5,
          id: 4
        }
      ];

    });


    this.menu.swipeEnable(true);
    this.menu.enable(false);
  }

  onSlideNext() {
    if(this.slides.isEnd()){
      this.onLastSlide();
      return;
    }

    this.slides.slideNext(300);
    
    if(this.slides.isEnd()){
      this.showLoginButton = true;
    }
    
  }

  onSlidePrev(fab:any = false) {
    
    if(this.slides.isEnd()){
      this.showLoginButton = false;
    }

    if(fab){
      fab.close();
    }
    this.slides.slidePrev(300)
  }

  onLastSlide() {
    //this.slides.slideTo(5, 300)
    this.openAuthPage();
  }

  skipForward() {
    this.openAuthPage();
  }

  openHomePage() {
    //this.navCtrl.setRoot('page-home1');
    this.navCtrl.setRoot('page-auth');
  }

  openAuthPage() {
    this.navCtrl.setRoot('page-auth');
  }

  openRegisterPage() {
    this.navCtrl.setRoot('page-auth',{tabIndex: 1});
  }

  ionViewDidLoad() {

  }

}



